<template>
    <b-container fluid>


        <b-form>
            <b-row class="mt-2">
                <b-col sm="2" offset="2">
                    <b-input v-model="newHeader" class="mb-2 mr-sm-2 mb-sm-0" placeholder="header"></b-input>
                </b-col>
                <b-col sm="2">
                    <b-input v-model="newContent" placeholder="content"></b-input>
                </b-col>


                <b-col sm="1">
                    <b-button variant="primary" size="lg" @click="change">Edit</b-button>
                    <b-button variant="primary" size="lg" @click="cancle">Cancle</b-button>
                </b-col>
            </b-row>
        </b-form>
    </b-container>
</template>

<script>
    import {mapActions} from 'vuex';

    export default {
        name: "Edit",
        props: {
            header: {
                type: String,
                default: ''
            },
            passport: {
                type: String,
                default: ''
            }
        },
        data() {
            return{
                newHeader: '',
                newContent: ''

            }
        },
        methods: {
            ...mapActions(['change_post']),

            change: function() {
                const post = JSON.stringify({header: this.newHeader, content: this.newContent});


                    this.change_post({id: this.$route.params.id, post: post});

                this.newUser = '';
                this.newMessage = '';
            },
            cancle: function () {
                this.$router.push({path: `/home`});
            }
        },
        mounted: function () {
            this.newUser = this.user;
            this.newMessage = this.message;
        }
    }
</script>

